<template>
    <div>
<el-card class="box-card" style="margin-top:20px;">
      <template>
        <el-table id="tab" :data="order" stripe style="width: 100%">
          <el-table-column prop="order_id" label="#" width="50"></el-table-column>
          <el-table-column prop="order_number" label="订单编号" width="400"></el-table-column>
          <el-table-column prop="order_price" label="订单价格" width="100"></el-table-column>
          <el-table-column prop="is_send" label="是否付款" width="100">

          </el-table-column>
          <el-table-column prop="is_send" label="是否发货" width="100"></el-table-column>
          <el-table-column prop="create_time" label="下单时间" width="120"></el-table-column>
          <el-table-column prop="add_time" label="创建时间" width="200"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" circle></el-button>
              <el-button
                type="danger"
                icon="el-icon-delete"
                @click="del(scope.row.goods_id)"
                circle
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
<div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[3,5,7,10]"
        background
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totals"
      ></el-pagination>
    </div>
    </el-card>
    </div>
</template>

<script>
export default {
    data() {
        return {
                order:[],
                totals:""
        };
    },
    created() {

    },
    mounted() {
            this.$http.get('orders?pagenum=3&pagesize=10').then((res) => {
                console.log(res)
                    this.order = res.data.data.goods
                    console.log(res.data.data.goods)
                    this.totals = res.data.data.total;
            })
            this.order.forEach((i, k) => {
                console.log(11111)
          var newdata = new Date(i.create_time);
          console.log(newdata);
          let date = new Date(newdata);
          let y = date.getFullYear();
          let MM = date.getMonth() + 1;
          MM = MM < 10 ? "0" + MM : MM;
          let d = date.getDate();
          d = d < 10 ? "0" + d : d;
          let h = date.getHours();
          h = h < 10 ? "0" + h : h;
          let m = date.getMinutes();
          m = m < 10 ? "0" + m : m;
          let s = date.getSeconds();
          s = s < 10 ? "0" + s : s;
          var da = y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;

          i.create_time = da;
        });
    },
    methods: {
            handleSizeChange(val){
                
                 




            },
            handleCurrentChange(val){
                // alert(val)                    
                alert(val)
this.$http.get(`/orders?pagenum=${val}&pagesize=10`).then(res => {
        this.order = res.data.data.goods;
        this.totals = res.data.data.total;

      });
            }
    }
};
</script>

<style scoped>

</style>



