import { data } from 'autoprefixer';
<template>
  <div>
    <el-card class="box-card" style="margin-top:20px;">
      <template>
        <el-table id="tab" :data="goods" stripe style="width: 100%">
          <el-table-column prop="goods_id" label="#" width="50"></el-table-column>
          <el-table-column prop="goods_name" label="商品名称" width="600"></el-table-column>
          <el-table-column prop="goods_price" label="商品价格" width="150"></el-table-column>
          <el-table-column prop="goods_weight" label="商品重量" width="150"></el-table-column>
          <el-table-column prop="add_time" label="创建时间" width="200"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" circle></el-button>
              <el-button
                type="danger"
                icon="el-icon-delete"
                @click="del(scope.row.goods_id)"
                circle
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
<div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[3,5,7,10]"
        background
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totals"
      ></el-pagination>
    </div>
    </el-card>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      goods: [],
      totals: "",
      pagenum: ""
    };
  },
  created() {},
  mounted() {
    this.$http
      .get("/goods?pagenum=1&pagesize=10")
      .then(res => {
        console.log(res);
        this.goods = res.data.data.goods;
        this.totals = res.data.data.total;
        this.goods.forEach((i, k) => {
          var newdata = new Date(i.add_time);
          console.log(newdata);
          let date = new Date(newdata);
          let y = date.getFullYear();
          let MM = date.getMonth() + 1;
          MM = MM < 10 ? "0" + MM : MM;
          let d = date.getDate();
          d = d < 10 ? "0" + d : d;
          let h = date.getHours();
          h = h < 10 ? "0" + h : h;
          let m = date.getMinutes();
          m = m < 10 ? "0" + m : m;
          let s = date.getSeconds();
          s = s < 10 ? "0" + s : s;
          var da = y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;

          i.add_time = da;
        });
      })
      .catch(err => {});
    this.$http.get("/menus").then(res => {
      console.log(res);
    });
  },
  methods: {
    axi() {
      this.$http.get("goods?pagenum=1&pagesize=10").then(res => {
        console.log(res);
        this.goods = res.data.data.goods;
      });
    },
    handleSizeChange(val) {
      this.$http.get(`/goods?pagenum=1&pagesize=${val}`).then(res => {
        console.log(val);
        this.goods = res.data.data.goods;
        this.totals = res.data.data.total;
        this.goods.forEach((i, k) => {
          var newdata = new Date(i.add_time);
          console.log(newdata);
          let date = new Date(newdata);
          let y = date.getFullYear();
          let MM = date.getMonth() + 1;
          MM = MM < 10 ? "0" + MM : MM;
          let d = date.getDate();
          d = d < 10 ? "0" + d : d;
          let h = date.getHours();
          h = h < 10 ? "0" + h : h;
          let m = date.getMinutes();
          m = m < 10 ? "0" + m : m;
          let s = date.getSeconds();
          s = s < 10 ? "0" + s : s;
          var da = y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;

          i.add_time = da;
        });
      });
    },
    handleCurrentChange(val) {
      this.$http.get(`/goods?pagenum=${val}&pagesize=10`).then(res => {
        this.goods = res.data.data.goods;
        this.totals = res.data.data.total;
        this.goods.forEach((i, k) => {
          var newdata = new Date(i.add_time);
          console.log(newdata);
          let date = new Date(newdata);
          let y = date.getFullYear();
          let MM = date.getMonth() + 1;
          MM = MM < 10 ? "0" + MM : MM;
          let d = date.getDate();
          d = d < 10 ? "0" + d : d;
          let h = date.getHours();
          h = h < 10 ? "0" + h : h;
          let m = date.getMinutes();
          m = m < 10 ? "0" + m : m;
          let s = date.getSeconds();
          s = s < 10 ? "0" + s : s;
          var da = y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;

          i.add_time = da;
        });
      });
    },
    del(id) {
      console.log(id);
      this.$http.delete(`/goods/${id}`).then(res => {
        console.log(res);
        this.axi();
      });
    }
  }
};
</script>

<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
</style>



