<template>
    <div class="aps">
            <el-container>
  <el-header>
      <img src="../assets/heima.b5a855ee.png" class="logo" alt="">
      电商后台管理系统
       <el-button type="info" @click="out()">退出</el-button>
  </el-header>
  
  <el-container>
<!--       
<el-aside class="aside"> -->



<el-menu default-active="1-4-1" background-color="#333744" text-color="white" class="el-menu-vertical-demo"    :collapse="isCollapse">
        <div class="top-bar" @click="handle()">|||</div>

            <el-submenu :index="cid[key]" v-for="(item,key) in List" :key="key">
                <template slot="title" 
                >
                  <i :class="icon[key].icon">
                    </i>
                    <span text-color="blue">{{item.authName}}</span>
                </template>
                <el-menu-item-group v-for="(i,k) in item.children" :key="k">
                  <el-menu-item index="1-1" @click="router-link(i.path)">
                    
                    <span text-color="blue">
                      <i class="el-icon-menu">
                    </i>
                    {{i.authName}}</span>
                  </el-menu-item>
                </el-menu-item-group>
                
              </el-submenu>
  
  
</el-menu>




<!-- </el-aside> -->
<el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
  
</el-radio-group>

    <el-main>
        <router-view></router-view>
    </el-main>
  </el-container>
</el-container>

    </div>
</template>

<script>
export default {
    data() {
        return {
            isCollapse: false,
            List:[],
            index:0,
            cid:[

              1,2,3,4,5
            ],
            icon:[
              {"icon":"el-icon-s-grid"},
              {"icon":"el-icon-box"},
              {"icon":"el-icon-shopping-bag-1"},
              {"icon":"el-icon-notebook-2"},
              {'icon':'el-icon-chat-line-round'}
            ]
            
        };
    },
    created() {

    },
    mounted() {
     this.$message({
          message: '登录成功',
          type: 'success'
        });
      this.$http.get('/menus').then((res) => {
            console.log(res)
            this.List = res.data.data
            this.List.forEach((i,k)=>{
                var obj = [this.index++]
                
                console.log(obj)
                console.log(i)
            })
            console.log(this.List)
      })
    },
    methods: {
        out(){
            this.$message({
          showClose: true,
          message: '您已退出后台管理系统',
          type: 'warning'
        });
        sessionStorage.removeItem('token')
        this.$router.push('/login')
        },
        link(p){
            this.$router.push('/'+p);
        },
        handle(key, keyPath) {
        this.isCollapse = !this.isCollapse;
      },
    }
};
</script>

<style scoped>

.el-button--info{
    float: right;
    margin-top: 10px;


}

.el-menu{
    background-color:  #333744;

}
.el-main{
  /* padding-left: 1px; */
  z-index: 10000;
}
.el-submenu{
  width: 200px;
  
}
.el-menu--collapse{
  /* width: 50px; */
}
.el-aside{
        background-color: #333744;
}
.el-submenu__title{
    color: white;
}
.aside{
  height: 100%;
        background-color: #333744;
}
.logo{
    float: left;
    margin-right: 10px;
}
.aps{
    width: 100%;
    height: 100%;
}
.el-main{
    font-weight: 600;
}
.el-dialog__body div{
  margin-bottom: 10px;
}
.el-container{
    height: 100%;
}
    .el-header{

        line-height: 60px;
        color:white;
        font-size: 18px;
        
        background-color: #373d41;
    }
    .el-main{
        background-color: #eaedf1;
    }
    .top-bar{
    background-color: #4a5064;
    color: #fff;
    font-size: 12px;
    text-align: center;
    line-height: 22px;
    cursor: pointer;
    width: 100%;
    letter-spacing: .12em;
    }
    .el-submenu__title{
      width: 200px !important;
    }
</style>



