<template>
    <div>
            <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/list' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="#/roles">权限管理</a>
      </el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb>
    <br><br>
    <el-card class="box-card" style="margin-top:20px;">
        <template>
   <el-table 
   id="tab"
    :data="list"
    stripe
    style="width: 100%">
    <el-table-column
      prop="id"
      label="#"
      width="50">
    </el-table-column>
    <el-table-column
      prop="authName"
      label="商品名称"
      width="300">
    </el-table-column>
    <el-table-column
      prop="path"
      label="路径"
      width="300">
    </el-table-column>
    <el-table-column
      label="权限等级">
        <template slot-scope="scope">

            <span class="el-tag--mini el-tag" v-if="scope.row.level==0">一级</span>
            <span class="el-tag--mini el-tag1" v-if="scope.row.level==1">二级</span>
            <span class="el-tag--mini el-tag2" v-if="scope.row.level==2">三级</span>
        </template>
    </el-table-column>
  </el-table>
</template>
    </el-card>
    </div>
</template>

<script>
export default {
    data() {
        return {
                list:[]
        };
    },
    created() {

    },
    mounted() {
            this.$http.get('rights/list').then((res) => {
                console.log(res)
                this.list = res.data.data
            }).catch((err) => {
                
            });
    },
    methods: {

    }
};
</script>

<style scoped>
.el-tag--mini {
    height: 20px;
    padding: 0 5px;
    line-height: 19px;
}
.el-tag {
    background-color: #ecf5ff;
    border-color: #d9ecff;
    display: inline-block;}
.el-tag1{
        background-color: #f0f9eb;
    border-color: #e1f3d8;
    color: #67c23a;
}
.el-tag2{
    background-color: #fdf6ec;
    border-color: #faecd8;
    color: #e6a23c;
}
</style>



