import axios from 'axios';
<template>
  <div class="binds">
    <el-form status-icon ref="ruleForm" class="demo-ruleForm">
      <el-form-item prop="pass">
        <el-input type="text" v-model="username"  prefix-icon="el-icon-s-custom"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input type="password" prefix-icon="el-icon-lock" v-model="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm()">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
      <div class="imges">
        <img src="../assets/logo.png" alt srcset class="img" />
      </div>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "admin",
      password: "123456"
    };
  },
  created() {},
  mounted() {},
  methods: {
    submitForm() {
      var obj = { username: this.username, password: this.password };
      this.$http
        .get(`login?username=${this.username}&password=${this.password}`)
        .then(res => {
          console.log(res);
          console.log(res.data.meta);
          console.log(res.data.meta.msg);
          if (res.data.meta.msg == "登录成功") {
            console.log(res.data.data)
            sessionStorage.setItem("token", res.data.data.token);
            this.$router.push("/list");
          } else {
            this.$message({
              showClose: true,
              message: "您输入的账号或密码有误,请重新输入",
              type: "error"
            });
          }
        })
        .catch(err => {});
    }
  }
};
</script>

<style scoped>
.el-form {
  padding-left: 20px;
  text-align: right;
}
.binds {
  width: 100%;
  height: 100%;
  position: relative;
  background: url("https://seopic.699pic.com/photo/40006/2210.jpg_wh1200.jpg");
}
.el-form {
  box-sizing: border-box;
  width: 500px;
  padding-top: 90px;
  background-color: white;
  height: 300px;
  padding-right: 20px;
  border-radius: 5px;
  position: absolute;
  left: 40%;
  top: 40%;
  position: relative;
}
.imges {
  position: absolute;
  top: -90px;
  left: 175px;
  padding: 20px;
  border-radius: 50%;
  background-color: blue;
  width: 100px;
  height: 100px;
  border: 5px white solid;
  overflow: hidden;
}
.img {
  text-align: center;
  width: 100%;
  height: 100%;
}
</style>



