      <tr>
        <td style="width:30px;">#</td>
        <td style="width:200px;">姓名</td>
        <td style="width:200px;">邮箱</td>
        <td style="width:200px;">电话</td>
        <td style="width:200px;">角色</td>
        <td style="width:200px;">状态</td>
        <td style="width:200px;">操作</td>
      </tr>
      <tr v-for="(item,key) in data" :key="key">
        <td style="width:30px;">{{item.id}}</td>
        
        <td style="width:200px;">{{item.username}}</td>
        <td style="width:200px;">{{item.email}}</td>
        <td style="width:200px;">{{item.mobile}}</td>
        <td style="width:200px;">{{item.role_name}}</td>
        <td style="width:200px;">
          <el-switch v-model="item.mg_state" active-color="#13ce66" inactive-color="#cccccc" @change="states(item)"></el-switch>
        </td>
        <td style="width:200px;">
          <el-button type="primary" icon="el-icon-edit" @click="show(item)" circle></el-button>
          <el-button type="danger" icon="el-icon-delete" @click="del(item.id)" circle></el-button>
          <el-tooltip class="item" effect="dark" content="Top Center 提示文字" placement="top">
    </el-tooltip>
    <el-button  type="warning" icon="el-icon-setting" circle slot="reference"></el-button>
  
        </td>
      </tr>
    </table>
