        :page-sizes="[2, 3, 4, 5]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
      <!-- 添加数据的弹框 -->
    <el-dialog title="添加数据" :visible.sync="dialogVisible" >
      <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="用户名">
      <el-input v-model="username" :value="user" label="用户名"></el-input>
  </el-form-item>
  <el-form-item label="密码">
      <el-input v-model="password" placeholder="请输入密码" label="用户名"></el-input>
  </el-form-item>
  <el-form-item label="邮箱">
      <el-input v-model="email" placeholder="请输入邮箱" label="邮箱"></el-input>
  </el-form-item>
  <el-form-item label="手机号">
      <el-input v-model="call" placeholder="请输入电话" label="用户名"></el-input>
  </el-form-item>
  </el-form>、

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="sub()">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改数据的弹框 -->
    <el-dialog title="修改数据" :visible.sync="dialog" width="30%" >
