      <el-input v-model="username" :value="user" label="用户名" disabled></el-input>
  </el-form-item>
  <el-form-item label="邮箱">
      <el-input v-model="email" placeholder="请输入邮箱" label="邮箱"></el-input>
  </el-form-item>
  <el-form-item label="手机号">
      <el-input v-model="call" placeholder="请输入电话" label="用户名"></el-input>
  </el-form-item>
  </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="edit()">确 定</el-button>
      </span>
    </el-dialog>
    
  </div>
</template>

