        this.call = item.mobile;
        this.userid = item.id;
        console.log(this.userid)
    },
    // 对数据进行修改数据
    edit(){
      
       this.$http.put(`/users/${this.userid}`,{email:this.email,mobile:this.call}).then((res) => {
        console.log(res)
            this.$http.get(`/users?pagenum=1&pagesize=2`).then(res => {
            console.log(res);
            this.data = res.data.data.users;
            console.log(res.data.data.total);
            this.total = res.data.data.total;
          });
      })
      this.dialog = !this.dialog
    },
