    sub() {
      this.$http.post('/users',{
        username: this.username,
        password: this.password,
        email: this.email,
        mobile: this.call
      }).then((res) => {
        
      this.dialogVisible = !this.dialogVisible;
    this.$http.get(`/users?pagenum=1&pagesize=2`).then(res => {
      console.log(res);
      this.data = res.data.data.users;
      console.log(res.data.data.total);
      this.total = res.data.data.total;
    });
          console.log(res)
      })
      console.log(obj);
      this.username = "";
      this.password = "";
      this.email = "";
      this.call = "";

      this.dialogVisible = !this.dialogVisible;
    },
    // 删除数据
    del(id){
      console.log(id)

              this.$http.delete(`/users/${id}`).then((res) => {
                console.log(res)
              }).catch((err) => {
                
              });
              this.$http.get(`/users?pagenum=1&pagesize=2`).then(res => {
              console.log(res);
              this.data = res.data.data.users;
              console.log(res.data.data.total);
              this.total = res.data.data.total;
            });
            }
  },
  watch:{
      handler:{
        // 监听input数据
        input(){
          console.log(1)
          if(!this.input){
            this.$http.get(`/users?pagenum=1&pagesize=2`).then(res => {
              console.log(res);
              this.data = res.data.data.users;
              console.log(res.data.data.total);
              this.total = res.data.data.total;
            });
          }else{
          console.log(1)

          }
        }
      },
      deep:true
  }
};
</script>

<style scoped>
.box {
    width: 400px;

    
  }
  .top {
      text-align: center;
    }

    .left {
      float: left;
      width: 60px;
    }

    .right {
      float: right;
      width: 60px;
    }

    .bottom {
      clear: both;
      text-align: center;
    }

    .item {
      margin: 4px;
    }

    .left .el-tooltip__popper,
    .right .el-tooltip__popper {
      padding: 8px 10px;
    }
.add{
  position: fixed;

}
.key {
  width: 300px;
}
.el-input{
  margin-top: 7px;
}
#put {
  float: left;
}
.el-breadcrumb {
  margin-bottom: 50px;
}
.el-button--default {
  float: left;
  margin-top: -40px;
  margin-left: 295px;
}
.el-input__inner{
  margin: 5px;
}
.tab {
  margin-top: 50px;
  border-radius: 10px;
}
.tab td {
  padding: 10px;
  font-weight: 300;
}
.el-dialog__body input {
  margin-bottom: 10px;
}
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
