<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/list' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="#/roles">权限管理</a>
      </el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <br><br>
    <el-button type="primary" class="adds" style="margin-left:400px;margin-top:-40px;" @click="add()">添加角色</el-button>
    <el-card class="box-card" style="margin-top:20px;">
        <template>
  <el-table
    :data="list"
    style="width: 100%">
    <el-table-column type="expand">
        <!-- 点击展开卡片 -->


      <!-- <template slot-scope="props">
        
      </template> -->
    </el-table-column>
    <el-table-column
      prop="id"
      label="#"
      width="50">
    </el-table-column>
    <el-table-column
      prop="roleName"
      label="角色名称"
      width="300">
    </el-table-column>
    <el-table-column
      prop="roleDesc"
      label="角色职务"
      width="300">
    </el-table-column>
    <el-table-column
      label="操作">
      <template slot-scope="scope">
        <el-button type="primary" @click="redit(scope.row)">编辑按钮</el-button>
              <el-button type="danger" @click="open(scope.row.id)">删除按钮</el-button>
              <el-button type="warning">权限分配</el-button>
      </template>
      
    </el-table-column>
  </el-table>
</template>
 <el-dialog title="添加数据" :visible.sync="dialog" width="50%" >
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="添加角色" prop="name">
    <el-input v-model="ruleForm.name"></el-input>
  </el-form-item>
  <el-form-item label="角色描述" prop="desc">
    <el-input v-model="ruleForm.desc"></el-input>
  </el-form-item>
    <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
</el-dialog>
<el-dialog title="修改数据" :visible.sync="dialogs" width="50%" >
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="添加角色" prop="name">
    <el-input v-model="ruleForm.name"></el-input>
  </el-form-item>
  <el-form-item label="角色描述" prop="desc">
    <el-input v-model="ruleForm.desc"></el-input>
  </el-form-item>
    <el-form-item>
    <el-button type="primary" @click="editname('ruleForm')">立即创建</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
</el-dialog>
    </el-card>
    <!-- <el-tree
  :data="tree"
  show-checkbox
  default-expand-all
  node-key="authName"
  ref="tree"
  highlight-current
  :props="">
  sadadasd
    <template slot-scope="scope">
    {{scope.row.authName}}
    </template>

</el-tree> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog:false,
      dialogs:false,
      id:"",
      tree:[],
      ruleForm: {
          name: '',
          desc: ''
        },
        rules: {
          name: [
            { required: true, message: '请输入商品名称', trigger: 'blur' },
            { min: 4, max: 6, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          desc: [
            { required: true, message: '请输入商品描述', trigger: 'blur' },
            { min: 4, max: 8, message: '长度在 4 到 8 个字符', trigger: 'blur' }
          ]
        },
            list:[]
    };
  },
  created() {},
  mounted() {
    
      this.$http.get('roles').then((res) => {
            console.log(res)
            this.list = res.data.data;
            
console.log(this.list)
      })
      this.$http.get('rights/tree').then((res) => {
          console.log(res)
          this.tree = res.data.data
      })
      this.$http.get('roles').then((res) => {
        console.log(res)
      }).catch((err) => {
        
      });
      
  },
  methods: {
    qie(item){
      let r = 90;
      $this.css('transform','rotate('+r+ 'deg)')

    },
    open(id){
          this.$http.delete(`roles/${id}`).then((res) => {
              console.log(res)
                this.axi()
          })
        
    },
    redit(item){
      console.log(item)
        this.dialogs = true;
        this.ruleForm.name = item.roleName;
        this.ruleForm.desc = item.roleDesc;
        this.id = item.id;
    },
    editname(item){
      
        this.$http.put(`roles/${this.id}`,{roleDesc:this.ruleForm.desc,roleName:this.ruleForm.name}).then((res) => {
            console.log(res)
            this.dialogs = false
            this.axi();
        })
    },
    submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
              this.$http.post('roles',{roleDesc:this.ruleForm.desc,roleName:this.ruleForm.name}).then((res) => {
                this.dialog = false;

                          this.$message({
                message: '角色添加成功',
                type: 'success'})
              })
                this.axi()
          } else {
            this.$message({
          message: '用户取消',
          type: 'success'
        });
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
    add(){
        this.dialog = true;
        this.ruleForm.name = '';
        this.ruleForm.desc = '';
    },
    axi(){
this.$http.get('roles').then((res) => {
            console.log(res)
            this.list = res.data.data
console.log(this.list)
      })
    }
  }
};
</script>

<style scoped>
.swit{
  width: 100%;
  height: 50px;
}
</style>



